import { FC } from 'react';

import {
  BUTTON_AS_ANCHOR,
  BUTTON_SIZE_LARGE,
  BUTTON_VARIANT_TERTIARY,
} from '#components/Button/Button.constants';
import Icon, { SIZE_SMALL } from '#components/Icon';

import { Link } from './JumpLink.styles';
import { JumpLinkProps } from './JumpLink.types';

import ScrollSVG from '#icons/vertical-align-top-16.svg';

const JumpLink: FC<JumpLinkProps> = ({ elementId, text, svg = null }) => (
  <Link
    forwardedAs={BUTTON_AS_ANCHOR}
    variant={BUTTON_VARIANT_TERTIARY}
    size={BUTTON_SIZE_LARGE}
    href={`#${elementId}`}
    icon={<Icon svg={svg || ScrollSVG} size={SIZE_SMALL} />}
  >
    {text}
  </Link>
);

export default JumpLink;
